.footer{
    width: 100%;
    height: vw(222);
    background-color: #2A2A2A;
    padding: vw(22) 0;
}

.footer__container{
    display: flex;
    align-items: center;
}

.footer__info{
    display: flex;
    flex-direction: column;
    width: vw(275);
}

p.footer__text{
    font-size: vw(14.8);
    font-family: 'Montserrat', sans-serif;
    color: white;
    font-size: vw(12);
    line-height: 120%;
    letter-spacing: vw(1);
    padding-top: vw(38);
}

.footer__logo{
    width: vw(92);
    height: vw(44.88);

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.footer__right{

    .menu__nav{
        width: vw(600);
        margin-left: 0;
    
        a.nav__item{
            font-size: vw(18);
        }
    }

}

.footer__right__info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: vw(45);

    .footer__right__text{
        p.footer__text{
            padding-top: 0;
        }
    }

    a.footer__number{
        font-family: 'Montserrat', sans-serif;
        color: #57C54E;
        font-size: vw(19);
        font-family: 500;
    }
}

.footer__right__last{
    margin-top: vw(45);
    display: flex;
    justify-content: space-between;
    align-items: center;

    .map__text{
        display: flex;
        align-items: center;

        .map__images{
            width: vw(21.46);
            height: vw(21.46);

            img{
                width: 100%;
                height: 100%;
            }
        }

        p.footer__text{
            padding-top: 0;
            padding-left: vw(5);
        }
    }
}

.search{
    width: vw(205);
    height: vw(41);
    border-radius: vw(40);
    border: vw(0.4) solid #fff;
    display: flex;
    align-items: center;

    input{
        width: 100%;
        outline: none;
        border: none;
        height: vw(41);
        background-color: transparent;
        padding-left: vw(15);
        font-size: vw(12);
        color: #888888;
        font-family: 'Montserrat', sans-serif;
    }

    .search__ico{
        width: vw(22.2);
        height: vw(22.2);
        margin-right: vw(15);
        cursor: pointer;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

::-webkit-search-decoration,
::-webkit-search-cancel-button,
::-webkit-search-results-button,
::-webkit-search-results-decoration { 
    display: none; 
}