.product__info{
    padding: vw(55) 0;

    p.header{
        text-align: center;
        text-transform: uppercase;
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        color: #222222;
        line-height: 110%;
        font-size: vw(27);
    }

    .product__info__container{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
    }

    .product__info__item{
        margin-top: vw(30);
        cursor: pointer;
        width: vw(233);
        display: flex;
        flex-direction: column;
        align-items: center;

        .product__info__image{
            width: vw(111);
            height: vw(111);
            
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        p.product__info__text{
            font-size: vw(11);
            font-family: 'Montserrat', sans-serif;
            font-weight: 500;
            margin-top: vw(11);
            text-align: center;
        }
    }
}
