.swiper-slide{
    background: #262626;
    width: vw(421);
    height: vw(278);

    .slide__images{
        height: vw(278);
        width: vw(259);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .slide__info{
        position: absolute;
        right: 0;
        top: 0;
        height: vw(278);
        width: vw(180);
        padding: vw(14.8);

        p.info{
            padding-top: vw(25);
            color: white;
            font-size: vw(12);
            font-family: 'Montserrat', sans-serif;
            font-weight: 400;

            &:first-child{
                padding-top: 0;
            }
        }

        a.more-info{
            display: flex;
            align-items: center;
            padding-top: vw(55);
            color: white;
            font-size: vw(12);
            font-family: 'Montserrat', sans-serif;
            font-weight: 400;
            position: relative;

            &::after{
                content: '';
                position: absolute;
                bottom: vw(-5);
                left: 0;
                width: vw(93.5);
                height: vw(1);
                background-color: #fff;
            }

            .mi__images{
                width: vw(18);
                height: vw(6);
                margin-left: vw(5);

                img{
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}

.bestproject__container{
    padding: vw(55) 0;

    p.header{
        font-size: vw(27);
        text-transform: uppercase;
        margin-bottom: vw(29);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
    }
}

.slider__wrapper{
    position: relative;

    .swiper-button-prev{
        position: absolute;
        left: vw(-44);
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        width: vw(42);
        height: vw(28);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .swiper-button-next{
        position: absolute;
        right: vw(-44);
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        width: vw(42);
        height: vw(28);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}