.header__menu{
    background-color: #2B2B2B;
    height: vw(65);
    display: flex;
    align-items: center;
}

.menu__container{
    display: grid;
    grid-template-columns: vw(92) vw(425) vw(200) vw(70);
    align-items: center;

    .search{
        margin-left: vw(150);
        transition: .3s;

        &.disable{
            border: none;

            input{
                width: 0;
            }
        }
    }
}

a.menu__logo{
    width: vw(92);
    height: vw(44.88);

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.menu__nav{
    width: vw(400);
    display: flex;
    justify-content: space-between;
    margin-left: vw(25);

    a.nav__item{
        font-family: 'Montserrat', sans-serif;
        color: white;
        font-size: vw(12);
        transition: .3s color;

        &:hover{
            color: rgba(#fff, 0.6);
        }
    }
}

.header__intro{
    position: relative;
    background: url('../img/intro.png') no-repeat center;
    background-size: 100%;
    height: vw(455);
    width: 100%;
}

.intro__content{
    width: vw(850);
    height: vw(279);
    background: linear-gradient(90.18deg, #222222 3.44%, rgba(43, 43, 43, 0.3) 55.56%, rgba(255, 255, 255, 0.05) 81.76%);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: vw(42);

    p.title{
        text-transform: uppercase;
        font-size: vw(27);
        color: white;
        font-family: 'Montserrat', sans-serif;
        font-weight: 662;
        line-height: 120%;

        span{
            color: #57C54E;
        }
    }

    p.text{
        font-size: vw(19);
        color: white;
        font-family: 'Montserrat', sans-serif;
        font-weight: 400;
        margin-top: vw(29);
    }

    button.intro{
        width: vw(222);
        height: vw(52);
        border-radius: vw(33);
        background: linear-gradient(90deg, #067C48 0%, #3CBA92 100%);
        cursor: pointer;
        outline: none;
        border: none;
        text-transform: uppercase;
        color: white;
        font-family: 'Montserrat', sans-serif;
        font-size: vw(12);
        font-weight: 500;
        margin-top: vw(22);
    }
}

.intro__btn{
    position: absolute;
    bottom: vw(60);
    right: vw(258);
}

.intro__btn__images{
    width: vw(50);
    height: vw(51);

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}